import React from 'react';
import styled from 'styled-components';
import {useSpring, animated, config} from 'react-spring';

const Base = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 120px;
    text-align: center;
    padding: 8px;
    filter: grayscale(${props => props.active ? 0 : 100}%);
`;

const Picture = styled.div`
    width: 48px;
    height: 48px;
    background-color: ${props => props.theme.secondary.main};
    border-radius: 50%;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.5);
    background-image: url(${props => props.src});
    background-size: cover;
    background-position: center center;
    border: 4px solid ${props => props.theme.secondary.main};
`;

const Title = styled.div`
    padding-top: 6px;
    color: ${props => props.active ? 'black' : '#666'};
`;

const Modal = animated(styled.div`
    display: ${props => props.active ? 'block' : 'none'}
    position: absolute;
    width: 200px;
    height: 100px;
    background-color: white;
    z-index: 1500;
`);

const Blocker = animated(styled.div`
    position: fixed;
    top: -200% !important;
    left: -200% !important;
    width: 400%;
    height: 400vh;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 1400;
`);

const Module = props => {
    const [state, setState] = React.useState({active: false, position: []});

    const picture = React.useRef(null);
    const modal = React.useRef(null);

    const handleClick = () => {
        let x = picture.current.offsetLeft;
        x += picture.current.offsetWidth / 2;
        x -= 100;

        let y = picture.current.offsetTop;
        y += picture.current.offsetHeight / 2;
        y -= 50;

        setState({active: !state.active, position: [x, y]});
    };

    const modalStyle = useSpring({
        transform: 'scale(' + (state.active ? 1 : 0) + ',' + (state.active ? 1 : 0) + ')',
        left: state.active ? state.position[0] : 0,
        top: state.active ? state.position[1] : 0,
        config: config.wobbly
    });

    const blockerStyle = useSpring({
        transform: 'scale(' + (state.active ? 100 : 0) + ',' + (state.active ? 100 : 0) + ')',
        opacity: state.active ? 1 : 0,
    })

    return (
        <>
            <Blocker style={blockerStyle}/>
            <Modal ref={modal} style={modalStyle} active={state.active}/>
            <Base onClick={handleClick} active={props.active}>
                <Picture ref={picture} src={props.src}/>
                <Title active={props.active}>
                    {props.title}
                </Title>
            </Base>
        </>
    )
};

export default Module;