import React from 'react';
import styled from 'styled-components';
import Module from "./Module";

const Base = styled.div`
    padding: 0 16px;
`;

const ModuleGroup = styled.div`
    padding: 8px;
    background-color: #eee;
    display: flex;
    justify-content: space-evenly;
    border-radius: 0 0 4px 4px;
    flex-wrap: wrap;
`;

const ModuleGroupHeader = styled.div`
    text-align: center;
    background-color: ${props => props.theme.primary.main};
    padding: 8px;
    color: ${props => props.theme.primary.color};
    border-radius: 4px 4px 0 0;
`;

const Course = props => {
    const modules = [];

    const moduleInfo = {
        'Strom und Spannung': {
            url: 'https://www.stwz.ch/upload/rm/strom.jpg?_=1553532388000',
            active: true
        },
        'Ohmsches Gesetz': {
            url: 'https://banner2.cleanpng.com/20180412/yte/kisspng-ohm-s-law-series-and-parallel-circuits-electrical-law-5acfa52b6e37b9.8287530415235576754515.jpg',
            active: false
        },
        'Spannungsteiler': {
            url: 'https://upload.wikimedia.org/wikipedia/commons/thumb/c/c0/Einfacher-Spannungsteiler.svg/220px-Einfacher-Spannungsteiler.svg.png',
            active: false
        }
    }

    for(const key in moduleInfo) {
        modules.push(<Module key={key} title={key} src={moduleInfo[key].url} active={moduleInfo[key].active} />)
    }

    return (
        <Base>
            <ModuleGroupHeader>
                Grundlagen
            </ModuleGroupHeader>
            <ModuleGroup>
                {modules}
            </ModuleGroup>
        </Base>
    )
};

export default Course;