import React from 'react';
import NavBar from "./Components/NavBar";
import SideNav from "./Components/SideNav/";
import MainView from "./Components/MainView";
import ListItem from "./Components/SideNav/Components/ListItem";
import BottomBar from "./Components/BottomBar";
import Course from "./Components/Course";
import {ThemeProvider} from "styled-components";

function App() {
    const [active, setActive] = React.useState(false);

    const theme = {
        primary: {
            main: '#0d47a1',
            light: '#5472d3',
            dark: '#002171',
            color: '#ffffff'
        },
        secondary: {
            main: '#ffff00',
            light: '#ffff5a',
            dark: '#c7cc00',
            color: '#000000'
        }
    }

    return (
        <ThemeProvider theme={theme}>
            <>
                <SideNav active={active} onClick={() => setActive(!active)}>
                    <ListItem>
                        Profil
                    </ListItem>
                    <ListItem>
                        Einstellungen
                    </ListItem>
                </SideNav>
                <NavBar onClick={() => setActive(!active)}/>
                <MainView>
                    <Course />
                </MainView>
                <BottomBar />
            </>
        </ThemeProvider>
    )
}

export default App;
